<template>
  <div class="fund_wrapper">
    <Header></Header>

    <div class="fund_content">
      <el-row class="fund_title_wrapper">
        <el-col :span="12" class="fund_title">
          <p class="total_funds fl">总资产折合：</p>
          <p class="total_val fl">0.000415060 BTC</p>
          <p class="fund_rmb fl">≈ 125CNY</p>
        </el-col>
        <el-col :span="12" class="other_sel">
          <p class="fr mention_money" @click="goTo('提币地址管理')">提币地址管理</p>
          <p class="fr finance_record" @click="goTo('财务记录')">财务记录</p>
        </el-col>
      </el-row>
      <el-row class="filter_head">
        <el-col >
          <el-checkbox v-model="isHidden" class="filter_check">隐藏小额资产</el-checkbox>
          <el-tooltip class="item" effect="dark" content="小于0.001BTC" placement="top-start">
           <i class="el-icon-info explain"></i>
          </el-tooltip>
        </el-col>
      </el-row>
      <el-row class="funds_main">
        <el-table :data="funds" ref="table" highlight-current-row v-loading="fundsLoad" >
          <el-table-column align="center" :show-overflow-tooltip="true" prop="symbol" sortable label="币种" width="150"> </el-table-column>
          <el-table-column align="center" :show-overflow-tooltip="true" prop="total" label="总量" > </el-table-column>
          <el-table-column align="center" :show-overflow-tooltip="true" prop="frozen" label="冻结" > </el-table-column>
          <el-table-column align="center" :show-overflow-tooltip="true" sortable label="BTC估值" >
            <template slot-scope="scope">
              {{scope.row.judge}} ≈ {{scope.row.rmb}} CNY
            </template>
          </el-table-column>
          <el-table-column align="center" :show-overflow-tooltip="true" prop="lock" label="锁仓" > </el-table-column>
          <el-table-column align="center" :show-overflow-tooltip="true"  label="操作">
            <template slot-scope="scope">
              <span class="opera" @click="getChargeInfo(scope.row)">充币</span>
              <span class="opera" @click="getMentionInfo(scope.row)">提币</span>
              <span class="opera" @click="goTrade">交易</span>
            </template>
          </el-table-column>
        </el-table>
      </el-row>
    </div>

    <el-dialog title="充币信息" center :visible.sync="chargeMStaus" :close-on-click-modal="false" :close-on-press-escape="false" @close="closeFn('charge')" width="50%">
      <el-row class="charge_money_wrapper" v-loading="opearaStatus" element-loading-background="rgba(255, 255, 255, 1)" >
        <el-col  class="com_way">
          <div class="com_title">链名称</div>
          <div class="com_content">
            <div class="com_btn_com" :class="item.sel?'comTabActive':''" v-for="(item,index) in withdraw" :key="index" @click="tabAddress(index,'charge')">
              {{item.type}}<img class="active_img" v-if="item.sel" src="@/assets/img/com_active.png" alt="">
            </div>
          </div>
        </el-col>
        <el-col  class="com_way">
          <div class="com_title" v-if="selDetail.address">充币地址</div>
          <div class="com_content">
            <div class="address">{{selDetail.address || '暂无'}}</div>
            <div class="copy" v-clipboard:success="onCopy" v-clipboard:copy="selDetail.address" v-if="selDetail.address">复制</div>
            <!-- <div class="er_code">二维码</div> -->
          </div>
        </el-col>
        <el-col class="charge_status">查看<a >充币记录</a>跟踪状态</el-col>

        <el-col class="prompt">
          <p>温馨提示</p>
          <p> • 请勿向上述地址充值任何非USDT资产，否则资产将不可找回。 </p>
          <p> • USDT充币仅支持simple send的方法，使用其他方法（send all）的充币暂时无法上账，请您谅解。 </p>
          <p> • 您充值至上述地址后，需要整个网络节点的确认，1次网络确认后到账，2 次网络确认后可提币。</p>
          <p> • 最小充值金额：100 USDT ，小于最小金额的充值将不会上账且无法退回。 </p>
          <p> • 您的充值地址不会经常改变，可以重复充值；如有更改，我们会尽量通过网站公告或邮件通知您。 </p>
          <p> • 请务必确认电脑及浏览器安全，防止信息被篡改或泄露。</p>
        </el-col>
        
      </el-row>
    </el-dialog>

    <el-dialog title="提币信息" center="" @close="closeFn('mention')" :close-on-click-modal="false" :close-on-press-escape="false" :visible.sync="mentionMstatus" width="50%">
      <el-row class="mention_money_wrapper" v-loading="mentionLoading" element-loading-background="rgba(255, 255, 255, 1)">
        <el-col class="risk_tip ">
          <p class="risk_title"><i class="el-icon-warning"></i>风险提示</p>
          <p class="risk_content">您的提币操作一旦完成，对应的资产所有权将由您变更为目标地址所对应的账户所有人享有，请您务必在提币操作前，仔细核对提币地址信息，确保提币属于自愿行为，并确认不涉及任何传销、非法集资、诈骗等违法情形，谨防上当受骗，避免造成不必要的财产损失。</p>
        </el-col>
        <el-col class="com_way">
          <div class="com_title">链名称</div>
          <div class="com_content">
            <div class="com_btn_com" :class="item.sel?'comTabActive':''" v-for="(item,index) in mentionAddress" :key="index" @click="tabAddress(index, 'mention')">
              {{item.type}}
              <img class="active_img" v-if="item.sel" src="@/assets/img/com_active.png" alt="">
            </div>
          </div>
        </el-col>
        <el-col class="mention_address">
          <p class="mention_title">提币地址</p>
          <p class="mention_content">
            <el-input type="text" v-model="menaddress"></el-input>
          </p>
        </el-col>
        <el-col class="mention_num">
          <p class="num_title">
            <span class="fl num">数量</span>
            <span class="fr limit">限额： {{mentionInfo.limit}}</span>
            <span class="fr able">可用： {{available}}</span>
          </p>
          <p>
            <el-input type="text" v-model="mennum">
              <el-button slot="append" >{{mentionInfo.symbol}}</el-button>
            </el-input>
          </p>
        </el-col>
        <el-col class="other_sel">
          <p class="fee">
            <span class="fl fee_charge">手续费</span>
            <!-- <span class="fr fee_charge">{{mentionInfo.fee_min}} - {{mentionInfo.fee_max}}</span> -->
            <el-input type="text" :readonly="true" class="fee_input" v-model="mentionInfo.fee_default" >
              <el-button slot="append" >{{mentionInfo.symbol}}</el-button>
            </el-input>
          </p>
          <p class="acc_num">
            <span class="fl fee_charge">到账数量</span>
            <el-input type="text" class="fee_input" :readonly="true" :value="accoutNums.toFixed(8)">
              <el-button slot="append" >{{mentionInfo.symbol}}</el-button>
            </el-input>
          </p>
        </el-col>
        <el-col class="prompt" :span="18">
          <p>温馨提示</p>
          <p>• 请勿向上述地址充值任何非{{mentionInfo.symbol}}资产，否则资产将不可找回。</p>
          <p>• {{mentionInfo.symbol}}充币仅支持simple send的方法，使用其他方法（send all）的充币暂时无法上账，请您谅解。</p>
          <p>• 您充值至上述地址后，需要整个网络节点的确认，1次网络确认后到账，2 次网络确认后可提币。</p>
        </el-col>
        <el-col :span="6" >
          <el-button @click.native="mentionFn(mentionInfo)" v-loading="menBtnLoad" class="mention_btn fr">提币</el-button>
        </el-col>
      </el-row>
    </el-dialog>
  </div>
</template>
<script>
  import Mock from '@/utils/mock'
  import Header from '@/components/header/header'

  export default {
    components: {Header},
    props: {},
    inject:['reload'],
    data() {
      return {
        isHidden:false,
        funds:[],
        fundsLoad:true,               // 资产加载状态
        withdraw:[],                  // 充币地址
        selDetail:{},                 // 充币地址被选中时的数据
        opearaStatus:true,            // 充币加载状态
        mentionLoading:true,          // 提币加载状态

        mentionInfo:{},               // 提币信息
        mentionAddress:[],            // 提币地址
        mentionLinkSel:'',            // 选中的提币链名称

        chargeMStaus:false,           // 充币状态
        mentionMstatus:false,         // 提币状态

        available:'',                 // 可用
        menaddress:'',                // 提币地址单条
        mennum:'',                    // 提币数量
        menBtnLoad:false,             // 提币按钮状态
      }
    },
    methods: {
      // /* 表格手风琴效果 */
      // toogleExpand(row, str, type) {
      //   let $table = this.$refs.table;
      //   this.opearaStatus = true
      //   this.operaStr = ''
      //   if(type == 'main'){
      //     this.operaStr = str

      //     if(str == '1'){
      //       var data = {"symbol":row.symbol, "type":0}
      //       this.$store.dispatch('charge_info', data).then(res => {
      //         if(res.data.result == 0){
      //           this.withdraw = res.data.detail || []
      //           if(res.data.detail.length > 0){
      //             res.data.detail.forEach(item => {
      //               item.sel = false
      //             })
      //             res.data.detail[0].sel = true
      //             this.withdraw = res.data.detail
      //             this.selDetail = res.data.detail[0]
      //           }

      //           this.funds.map((item) => {
      //             if(row.id != item.id){
      //               $table.toggleRowExpansion(item, false)
      //               item.expansion = false
      //             }else{
      //               item.expansion = !item.expansion
      //             }
      //           })
      //           this.opearaStatus = false
      //           $table.toggleRowExpansion(row)

      //         }
      //       }).catch(err => {
      //         this.opearaStatus = false
      //       })
      //     }else if(str == '2'){
      //       this.getMentionInfo(row)
      //     }
      //   }else if(type == 'other'){
      //     this.operaStr = str
      //     console.log('ddddddd')
      //     for(var i = 0;i<this.withdraw.length;i++){
      //       if(index == i){
      //         this.withdraw[i].sel = true
      //         this.selDetail = this.withdraw[i]
      //       }else{
      //         this.withdraw[i].sel = false
      //       }
      //     }
      //   }
        
      // },

      // change(){
      //   this.opearaStatus = true
      // },

      // /* 切换地址 */
      tabAddress(index,str){
        switch(str){
          case 'charge':
            for(var i = 0;i<this.withdraw.length;i++){
              if(index == i){
                this.withdraw[i].sel = true
                this.selDetail = this.withdraw[i]
              }else{
                this.withdraw[i].sel = false
              }
            }
            break;
          case 'mention':
            var arrs = this.mentionAddress
            for(var i = 0;i<arrs.length;i++){
              if(index == i){
                arrs[i].sel = true
                this.mentionLinkSel = arr[i].type
                this.$set(arrs,i,arrs[i]);
              }else{
                arrs[i].sel = false
                this.$set(arrs,i,arrs[i]);
              }
            }
            this.mentionAddress = arrs
            break;
          default:
            break;
        }
      },

      /*跳转页面*/
      goTo(str){
        const breads = {name:'资产', url:'/funds'}
        localStorage.setItem('breads',JSON.stringify(breads))
        switch(str){
          case '财务记录':
            this.$router.push({path:'/financial',query:{name:str}})
            break;
          case '提币地址管理':
            this.$router.push({path:'/menAddress',query:{name:str}})
            break;
          default:
            break;
        }
      },
      goTrade(){
        this.$router.replace('/home/trade')
      },

      /* 资产列表 */
      getFunds(){
        var fundsObj = {wallet_type:0,"judge":"BTC"}
        this.$store.dispatch('funds_list', fundsObj).then(res => {
          // console.log('res资产', res)
          this.fundsLoad = false
          if(res.data.result == 0){
            this.funds = res.data.data || []
            if(this.funds.length > 0){
              for(var i = 0;i<this.funds.length;i++){
                this.funds[i].id = i
              }
            }
          }
        }).catch(err => {
          this.fundsLoad = false
        })
      },

      /* 充币信息 */
      getChargeInfo(row){
        this.chargeMStaus = true
        var data = {"symbol":row.symbol, "type":0}
        this.$store.dispatch('charge_info', data).then(res => {

          if(res.data.result == 0){
            this.withdraw = res.data.detail || []
            if(this.withdraw.length > 0){
              this.withdraw.forEach(item => {
                item.sel = false
              })
              this.withdraw[0].sel = true
              this.selDetail = this.withdraw[0]
            }
            // console.log('this.withdraw', this.withdraw)
          }
          this.opearaStatus = false
        }).catch(err => {
          this.opearaStatus = false
        })
      },

      /* 提币信息 */
      getMentionInfo(row){
        this.mentionMstatus = true
        this.available = row.total;

        var data = {"symbol":row.symbol}
        this.$store.dispatch('mention_info', data).then(res => {
          // console.log('res充提币信息', res)
          if(res.data.result == 0){
            this.mentionInfo = res.data
            this.mentionAddress = res.data.detail || []
            if(this.mentionAddress.length > 0){
              this.mentionAddress.forEach(item => {
                item.sel = false
              })
            }
            this.mentionLoading = false
            this.mentionAddress[0].sel = true
            this.mentionLinkSel = this.mentionAddress[0].type
          }
        }).catch(err => {
          this.mentionLoading = false
        })
      },

      /* 提币 */
      mentionFn(obj){
        if(this.menaddress == ''){
          this.$message.info('提币地址不能为空')
          return false
        }
        if(this.mennum == ''){
          this.$message.info('提币数量不能为空')
          return false
        }
        if(Number(this.mennum) <= Number(obj.fee_default)){
          this.$message.info('提币数量要高于手续费')
          return false
        }
        if(Number(this.mennum)>Number(this.available)){
          this.$message.info('提币数量不能超过可用余额')
          return false
        }
        
        // if(Number(this.fee) < Number(obj.fee_min) || Number(this.fee) > Number(obj.fee_max)){
        //   this.$message.info('提币手续费必须在'+ obj.fee_min + '-' + obj.fee_max + '范围内')
        //   return false
        // }
        
        var menInfo = {
          "symbol":obj.symbol, 
          "type":this.mentionLinkSel, 
          "to_address":this.menaddress, 
          "volume":this.mennum,
          "fee":obj.fee_default
        }
        this.menBtnLoad = true

        this.$store.dispatch('mention',menInfo).then(res => {
          this.menBtnLoad = false
          if(res.data.result == 0){
            this.$message.success('提币请求已提交')
            this.reload()
          }
        }).catch(err => {
          this.menBtnLoad = false
        })
      },

      /* 弹窗关闭回调 */
      closeFn(str){
        switch(str){
          case 'charge':
            this.opearaStatus = true
            break;
          case 'mention':
            this.mentionLoading = true
            break;
          default:
            break;
        }
        // console.log('11111')
        this.menaddress = ''
        this.mennum = ''
      },

      /* 复制 */
      onCopy (e) {
        // console.log('e', e)
        this.$message.success('成功复制到剪贴板')
      }
    },
    computed:{
      accoutNums:function(){
        var diff = Number(this.mennum) - Number(this.mentionInfo.fee_default);
        if(diff > 0){
          return diff
        }else{
          return 0
        }
      }
    },
    
    created() {
      this.getFunds()
    },
    mounted() {}
  };
</script>
<style lang="scss" scoped>
  @import '@/style/base.scss';
  
  .fund_wrapper{
    width: 100%;
    min-height: 100%;
    background-color: $other_bg;
    min-width: 1280px;
    padding-top: 60px;
    padding-bottom: 15px;
    box-sizing: border-box;
    .fund_content{
      width: 1200px;
      background-color: $light_font_color;
      margin:0 auto;
      .fund_title_wrapper{
        height: 50px;
        line-height: 50px;
        padding: 0 20px;
        border-bottom: 1px solid $border_dark_color;
        .fund_title{
          color: $light_title_color;
          font-size: $font16;
          .total_val{
            color: $light_title_color;
            font-size: $font16;
            margin-right: 10px;
          }
          .fund_rmb{
            color: $dark_attention_title_font_color;
            font-size: $font14;
          }
        }
        .other_sel{
          color: $active_font_color;
          font-size: $font14;
          >p{
            cursor: pointer;
          }
          .mention_money{
            margin-left: 30px;
          }
        }
        
      }
      .filter_head{
        padding:0 20px;
        line-height: 40px;
        .explain{
          vertical-align: middle;
          color: $dark_title_color;

        }
      }
      .funds_main{
        padding: 0 20px 100px;
        min-height: 500px;
        .opera{
          color: $active_font_color;
          font-size: $base_font;
          margin-right: 10px;
          cursor: pointer;
        }
      }
    }
    .charge_money_wrapper{
      width: 100%;
      padding: 0 0px 15px;
      box-sizing: border-box;
      min-height: 300px;
      
      .charge_status{
        color: $dark_title_color;
        text-align: left;
        height: 35px;
        line-height: 35px;
        font-size: $base_font;
        margin-bottom: 10px;
        >a{
          color: $active_font_color;
        }
      }
      .prompt{
        >p{
          color: $dark_title_color;
          text-align: left;
          height:20px;
          line-height: 20px;
          font-size: $base_font;
        }
      }
    }

    .mention_money_wrapper{
      width: 100%;
      padding: 15px;
      box-sizing: border-box;
      .risk_tip{
        width: 100%;
        min-height: 70px;
        max-height: 100px;
        overflow: auto;
        padding: 10px;
        box-sizing: border-box;
        border: 1px solid $border_risk_color;
        background-color: $risk_bg;
        margin-bottom: 15px;
        .risk_title{
          font-size: $base_font;
          color: $noraml_font_color;
          >i{
            color: $red_font_color;
            margin-right: 5px;
            font-size: $font14;
          }
        }
        .risk_content{
          font-size: $base_font;
          color: $noraml_font_color;
          padding-left: 19px;
          box-sizing: border-box;
        }
      }
      .mention_address{
        margin-bottom: 15px;
        .mention_title{
          color: $dark_title_color;
          text-align: left;
          font-size: $base_font;
          height: 35px;
          line-height: 35px;
        }
      }
      .mention_num{
        margin-bottom: 15px;
        .num_title{
          color: $dark_title_color;
          text-align: left;
          font-size: $base_font;
          height: 35px;
          line-height: 35px;
          .limit{
            margin-left: 25px;
          }
        }
      }
      .other_sel{
        display: flex;
        justify-content: flex-start;
        margin-bottom: 15px;
        .fee{
          width: 47%;
          margin-right: 3%;
        }
        .acc_num{
          width: 47%;
          margin-left: 3%;
        }
        .fee_charge{
          line-height: 35px;
          height: 35px;
          font-size: $base_font;
          color: $dark_title_color;
        }
      }
      .mention_btn{
        width: 150px;
        background-color: $user_btn_bg_btn;
        color: $light_font_color;
        font-size: $font14;
        text-align: center;
        margin-top: 10px; 
      }
    }

    .com_way{
      margin-bottom: 15px;
      .com_title{
        color: $dark_title_color;
        text-align: left;
        height: 35px;
        line-height: 35px;
        font-size: $base_font;
      }
      .com_content{
        box-sizing: border-box;
        display: flex;
        justify-content: flex-start;
        .com_btn_com{
          width: 100px;
          height: 40px;
          line-height: 40px;
          text-align: center;
          border:1px solid transparent;
          border-radius: 5px;
          font-size: $font14;
          color: $noraml_font_color;
          background-color: $border_dark_color;
          cursor: pointer;
          position: relative;
          margin-right:30px;
          .active_img{
            position: absolute;
            width: 16px;
            height: 20px;
            bottom: 0;
            right: 0;
          }
        }
        .comTabActive{
          border-color:$border_active_color;
          color: $active_font_color;
          background-color: $light_font_color; 
        }
        .address{
          line-height: 20px;
          height: 20px;
          font-size: $font16;
          color: $noraml_font_color;
          margin-right: 20px;
        }
        .copy{
          line-height: 20px;
          height: 20px;
          font-size: $font16;
          color: $active_font_color;
          margin-right: 20px;
          cursor: pointer;
        }
        .er_code{
          line-height: 20px;
          height: 20px;
          font-size: $font16;
          color: $active_font_color;
          margin-right: 20px;
          cursor: pointer;
        }
      }
      .comContent{
        flex-grow: 1;
        color: $noraml_font_color;
        line-height: 35px;
        .code_img{
          width: 110px;
          height: 110px;
          padding-top: 10px;
          box-sizing: border-box;
        }
      }
      
      .traded_single{
        font-size: $font14;
        color:$active_font_color;
        height: 35px;
        line-height: 35px;
      }
    }
  }
</style>


